.SvgContainer text{
    z-index: 100;
    user-select: none;
}
.KeyBoard{
    background-color: buttonface;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    display: none;
    justify-content: center;
    align-items: center;
    padding-top: .2em;
    padding-bottom: .2em;
    border-top: 2px solid gray;
    border-bottom: 2px solid gray;
}

.KeyBoard .btn-name{
    padding: 0em;
    text-align: center;
    user-select: none;
    color: black;
    font-size: .5em;
    /* display: none; */
}

.KeyBoard .btn-content{
    border: 1px solid gray;
    /* border-radius: 50%; */
    margin: .1em;
    width: 1.3em;
    height: 1.3em;
    text-align: center;
    user-select: none;
    color: gray;
    background-color: white;
    line-height: 1.3em;
    font-weight: bolder;
}