.TopBar{
    background-color: buttonface;
    display: flex;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    justify-content: flex-end;
    /* align-items: flex-end; */
    /* flex-flow: row; */
    z-index: 2;
}

.TopBar svg {
    color: gray;
    margin: .1em;
    border: 1px solid gray;
    padding: .3em;
}
.TopBar svg polygon{
    stroke: gray;
}

#FaVectorSquare, #FaRegCircle, #AiOutlineNodeIndex, #GrCursor{
    border: 1px solid buttonface;
}


/* Space between groups */
.TopBar .groupedBtn{
    /* border-left: 6px solid buttonface; */
    border-right: 6px solid buttonface;
    display: flex;
}


