.p1, .p2, .p3, .p4{
    position: absolute;
    width: .5em;
    height: .5em;
    margin: -.25em;
    left: 0px;
    top: 0px;
    border-radius: 50%;
    background-color: #00ff00;
    z-index: 101;

}
.p2{
    background-color: #ffff00;
}
.p3{
    background-color: #00ff00;
}

.p4{
    background-color: #66ccff;
    left: 100px;
    top: 100px;
}